.container_item_shopping {
    display: grid;
    grid-template-columns: 120px auto;
    // column-gap: 1.5em;
    margin-bottom: 1em;
    overflow: hidden;
    .image {
        width: 100%;
        padding-right: 5.5em;
        max-height: 455px;
        max-height: 60vh;
        overflow: hidden;
        display: flex;
        align-items: center;
        position: relative;
        flex-direction: column;
        img {
            max-width: 120px;
    height: 120px;
        }
    }
    .body {
        padding: 1em;
        // padding: 0.5em;
        .header,
        .data_body {
            display: grid;
            grid-template-columns: 1.4fr 0.5fr 0.5fr 0.7fr 0.5fr;
            gap: 1em;
            text-align: center;
        }
        .data_body {
            align-items: center;
            margin-top: 1.5em;
            color: gray;
            font-size: 0.8em;
            // align-items: center;
            // margin-top: 1.5em;
            // color: gray;
            // font-size: 0.8em;
            .input-field {
                margin: 0;
            }
            .quantity {
                display: flex;
                justify-content: center;
                input {
                    text-align: center;
                    max-width: 40px;
                    margin: 0;
                }
            }
            .delete_icon {
                display: flex;
                justify-content: center;
                cursor: pointer;
                span {
                    text-decoration: underline;
                    // font-style: ;
                }
            }
        }
    }
}

.container_item_shopping_preview {
    display: grid;
    margin-bottom: 1em;
    // border-radius: 8px;
    overflow: hidden;
    // box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    max-width: 435px;
    font-size: 0.9em;
    grid-template-columns: 113px auto;
    background-color: white;
    .image {
        width: 100%;
