        max-height: 60vh;
        overflow: hidden;
        display: flex;
        align-items: center;
        position: relative;
        flex-direction: column;
        img {
            max-width: 100%;
            height: 100px;
        }
    }
    .body {
        padding: 1em;
        // padding: 0.5em;
        .header,
        .data_body {
            display: grid;
            grid-template-columns: 1.4fr 0.5fr 0.5fr 0.7fr;
            text-align: center;
        }
        .data_body {
            align-items: center;
            margin-top: 1.5em;
            color: gray;
            font-size: 0.8em;
            .input-field {
                margin: 0;
            }
            .quantity {
                display: flex;
                justify-content: center;
                input {
                    text-align: center;
                    max-width: 40px;
                    margin: 0;
                }
            }
            .delete_icon {
                display: flex;
                justify-content: center;
                cursor: pointer;
                span {
                    text-decoration: underline;
                    // font-style: ;
                }
            }
        }
    }
}


/* Extra small devices (phones, 600px and down) */

@media only screen and (max-width: 600px) {
    .container_item_shopping {
        display: grid;

    grid-template-columns: 1fr !important;
        // grid-template-columns: 120px auto;
        // column-gap: 1.5em;
        margin-bottom: 1em;
        overflow: hidden;
        .image {
            width: 100%;
            padding-right: 5.5em;
            max-height: 455px;
            max-height: 60vh;
            overflow: hidden;
            display: flex;
            align-items: center;
            position: relative;
            flex-direction: column;
            img {
                max-width: 150px;
                height: 150px;
            }
        }
    }
}


